#banner h1 {
    height: 4.25rem;
    line-height: 4.25rem;
    .typewriter {
      max-width: 0;
      transition: max-width 200ms linear;
      display: inline-block;
      overflow: hidden;
      white-space: nowrap;
      border-right: 5px solid #fff;
      padding-right: 10px;
    }
}

@keyframes animated-cursor{
    from{border-right-color: rgba(255,255,255,.75);}
    to{border-right-color: rgba(255,255,255,0);}
}